<template>
  <div>
    <div class="ispu">
      <h1 class="content-header">{{ this.$route.name }}</h1>
      <base-header class="pb-6" type="primary"></base-header>

      <div class="container-fluid mt--5">
        <div class="row">
          <div class="col">
            <div class="card border-0">
              <div
                id="map-custom"
                class="map-canvas"
                style="height: calc(100vh - 512px); min-height: 350px"
              ></div>
            </div>
          </div>
        </div>

        <div class="row justify-content-start mb-4 ml-1">
          <h2 v-if="titleUnitKerja">Unit Kerja: {{ titleUnitKerja }}</h2>
        </div>
        <div class="row">
          <div class="col-xl-3 col-md-6">
            <div class="card card-red border-0">
              <!-- Card body -->
              <div class="card-body">
                <div class="row">
                  <div class="col">
                    <h5
                      class="card-title text-uppercase text-muted mb-0 text-white"
                    >
                      CO
                    </h5>
                    <span class="h3 font-weight-bold mb-0 text-white"
                      >{{ co }} ug/Nm3</span
                    >
                    <base-progress
                      class="progress-xs mt-3 mb-0"
                      type="success"
                      :value="coslider"
                    ></base-progress>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-xl-3 col-md-6">
            <div class="card card-blue border-0">
              <!-- Card body -->
              <div class="card-body">
                <div class="row">
                  <div class="col">
                    <h5
                      class="card-title text-uppercase text-muted mb-0 text-white"
                    >
                      PM 2.5
                    </h5>
                    <span class="h3 font-weight-bold mb-0 text-white"
                      >{{ pm25 }} ug/Nm3</span
                    >
                    <base-progress
                      class="progress-xs mt-3 mb-0"
                      type="success"
                      :value="pm25slider"
                    ></base-progress>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-xl-3 col-md-6">
            <div class="card card-red border-0">
              <!-- Card body -->
              <div class="card-body">
                <div class="row">
                  <div class="col">
                    <h5
                      class="card-title text-uppercase text-muted mb-0 text-white"
                    >
                      PM 10
                    </h5>
                    <span class="h3 font-weight-bold mb-0 text-white"
                      >{{ pm10 }} ug/Nm3</span
                    >
                    <base-progress
                      class="progress-xs mt-3 mb-0"
                      type="success"
                      :value="pm10slider"
                    ></base-progress>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-xl-3 col-md-6">
            <div class="card card-blue border-0">
              <!-- Card body -->
              <div class="card-body">
                <div class="row">
                  <div class="col">
                    <h5
                      class="card-title text-uppercase text-muted mb-0 text-white"
                    >
                      TSP
                    </h5>
                    <span class="h3 font-weight-bold mb-0 text-white"
                      >{{ tsp }} ppm</span
                    >
                    <base-progress
                      class="progress-xs mt-3 mb-0"
                      type="success"
                      :value="tspslider"
                    ></base-progress>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="row">
          <div class="col-xl-3 col-md-6">
            <div class="card card-blue border-0">
              <!-- Card body -->
              <div class="card-body">
                <div class="row">
                  <div class="col">
                    <h5
                      class="card-title text-uppercase text-muted mb-0 text-white"
                    >
                      NO2
                    </h5>
                    <span class="h3 font-weight-bold mb-0 text-white"
                      >{{ no2 }} ug/Nm3</span
                    >
                    <base-progress
                      class="progress-xs mt-3 mb-0"
                      type="success"
                      :value="no2slider"
                    ></base-progress>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-xl-3 col-md-6">
            <div class="card card-red border-0">
              <!-- Card body -->
              <div class="card-body">
                <div class="row">
                  <div class="col">
                    <h5
                      class="card-title text-uppercase text-muted mb-0 text-white"
                    >
                      SO2
                    </h5>
                    <span class="h3 font-weight-bold mb-0 text-white"
                      >{{ so2 }} ug/Nm3</span
                    >
                    <base-progress
                      class="progress-xs mt-3 mb-0"
                      type="success"
                      :value="so2slider"
                    ></base-progress>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-xl-3 col-md-6">
            <div class="card card-blue border-0">
              <!-- Card body -->
              <div class="card-body">
                <div class="row">
                  <div class="col">
                    <h5
                      class="card-title text-uppercase text-muted mb-0 text-white"
                    >
                      O3
                    </h5>
                    <span class="h3 font-weight-bold mb-0 text-white"
                      >{{ o3 }} ug/Nm3</span
                    >
                    <base-progress
                      class="progress-xs mt-3 mb-0"
                      type="success"
                      :value="o3slider"
                    ></base-progress>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import GoogleMapsLoader from "google-maps";
// import markerContent from "./marker.js";
// import marker from "../IEMS/marker.js";
import axios from "axios";
GoogleMapsLoader.KEY = "AIzaSyDL-imf29QugRQMnO6PHnrF5xmQsWhFSp8";

export default {
  beforeDestroy() {
    clearInterval(this.intervalSlider);
  },
  beforeMount() {
    this.getMap();
  },
  methods: {
    getSenData(id) {
      axios.get("http://45.127.133.75:3000/api/ispu/data/" + id).then((res) => {
        this.co = res.data[0].CO.toFixed(2);
        this.pm25 = res.data[0].PM2DOT5.toFixed(2);
        this.pm10 = res.data[0].PM10.toFixed(2);
        this.tsp = res.data[0].TSP.toFixed(2);
        this.no2 = res.data[0].NO2.toFixed(2);
        this.so2 = res.data[0].SO2.toFixed(2);
        this.o3 = res.data[0].O3.toFixed(2);
        this.coslider = (res.data[0].CO / 9000) * 100;
        this.pm25slider = (res.data[0].PM2DOT5 / 65) * 100;
        this.pm10slider = (res.data[0].PM10 / 150) * 100;
        this.tspslider = (res.data[0].TSP / 230) * 100;
        this.no2slider = (res.data[0].NO2 / 92.5) * 100;
        this.so2slider = (res.data[0].SO2 / 260) * 100;
        this.o3slider = (res.data[0].O3 / 120) * 100;
      });
    },
    getMap() {
      axios.get("http://45.127.133.75:3000/api/ispu/map").then((res) => {
        this.mapData = res.data;

        GoogleMapsLoader.load((google) => {
          this.initMap(google);
        });
      });
    },
    initMap(google) {
      let map,
        lat = 0.340199,
        lng = 116.472123,
        color = "#5e72e4";
      map = document.getElementById("map-custom");

      let centerLat = new google.maps.LatLng(0.340199, 116.472123);
      let mapOptions = {
        zoom: 5,
        scrollwheel: false,
        center: centerLat,
        mapTypeId: google.maps.MapTypeId.ROADMAP,
      };

      map = new google.maps.Map(map, mapOptions);
      var infowindow = null;
      var test = [];
      function clear() {
        test.forEach((m) => {
          m.setMap(null);
        });
      }
      const these = this;
      function renderMarker(z) {
        for (let i = 0; i < these.mapData.length; i++) {
          let pos = new google.maps.LatLng(
            these.mapData[i].LATITUDE,
            these.mapData[i].LONGITUDE
          );

          let url = "http://maps.google.com/mapfiles/ms/icons/orange-dot.png";
          let marker = [];
          if (z === i) {
            url = "http://maps.google.com/mapfiles/ms/icons/blue-dot.png";
            marker[i] = new google.maps.Marker({
              icon: { url: url },
              position: pos,
              map: map,
              animation: google.maps.Animation.DROP,
              title: these.mapData[i].SENSOR_NAME,
              id: these.mapData[i].SENSOR_ID,
            });
          } else {
            marker[i] = new google.maps.Marker({
              icon: { url: url },
              position: pos,
              map: map,
              animation: google.maps.Animation.DROP,
              title: these.mapData[i].SENSOR_NAME,
              id: these.mapData[i].SENSOR_ID,
            });
          }

          test.push(marker[i]);

          const that = these;
          google.maps.event.addListener(marker[i], "click", function() {
            that.getSenData(marker[i].id);
            that.titleUnitKerja = marker[i].title;
            clear();
            clearInterval(that.intervalSlider);
            that.intervalSlider = setInterval(() => {
              that.getSenData(marker[i].id);
            }, 1000);
            renderMarker(i);
          });
        }
      }
      renderMarker();
    },
  },
  mounted() {},
  data() {
    return {
      titleUnitKerja: "",
      mapData: [],
      co: 0,
      pm25: 0,
      pm10: 0,
      tsp: 0,
      no2: 0,
      so2: 0,
      o3: 0,
      coslider: 0,
      pm25slider: 0,
      pm10slider: 0,
      tspslider: 0,
      no2slider: 0,
      so2slider: 0,
      o3slider: 0,
      intervalSlider: undefined,
    };
  },
};
</script>
<style scoped>
.card-blue {
  background: linear-gradient(90deg, #0d3f7a 0%, #00aeef 100%);
}
.card-red {
  background: linear-gradient(90deg, #ef4123 0%, #fcb114 100%);
}
</style>
